//preloader

const preloader = document.querySelector('.preloader');

const bodyFixed = () => {
  document.body.classList.add('body-fixed');
};

const isPreloaderLoaded = () => {
  document.body.classList.remove('body-fixed');
  preloader.classList.add('hidden');
};

const preloaderAnim = {
  targets: preloader,
  delay: 1500,
  opacity: {
    value: 0,
    duration: 300,
    easing: 'linear',
  },
  offset: '+=100',
  begin: function() {
    bodyFixed();
  },
  complete: function () {
    isPreloaderLoaded();
  }
};

//animation by class init

const workScaleBig = document.querySelectorAll('.ScaleBigJs');
const workScaleMini = document.querySelectorAll('.ScaleMiniJs');

workScaleBig.forEach(item => {
  const workImgScroll = new Waypoint({
    element: item,
    handler: function() {
      const taskTranslation = anime({
        targets: item,
        scale: [1.5, 1],
        easing: 'linear',
        duration: 500,
      });
      this.destroy()
    },
    offset: '100%',
  });
});

workScaleMini.forEach(item => {
  const workImgScroll = new Waypoint({
    element: item,
    handler: function() {
      const taskTranslation = anime({
        targets: item,
        scale: [0.7, 1],
        easing: 'linear',
        duration: 500,
      });
      this.destroy()
    },
    offset: '100%',
  });
});

//home page animation

const mainTitle1 = document.querySelector('.home-main-title__name--first');
const mainTitle2 = document.querySelector('.home-main-title__name--second');
const logo = document.querySelector('.logo');
const mainLine = document.querySelector('.home-main-line');
const mainCenter = document.querySelector('.home-main-center');

const mainCenterAnim = {
  targets: mainCenter,
  translateY: '-50%',
  translateX: '-50%',
  scale: ['0', '1'],
  autoplay: true,
  easing: 'linear',
};

const mainTitle1Anim = {
  targets: mainTitle1,
  translateX: ['-150%', '-50%'],
  opacity: ['0', '1'],
  autoplay: true,
  easing: 'linear',
  delay: 200,
};

const mainTitle2Anim = {
  targets: mainTitle2,
  translateX: ['150%', '-50%'],
  opacity: ['0', '1'],
  autoplay: true,
  easing: 'linear',
};

const logoAnim = {
  targets: logo,
  opacity: ['0', '1'],
  autoplay: true,
  easing: 'linear',
};

const mainLineAnim = {
  targets: mainLine,
  translateX: '-50%',
  translateY: '-50%',
  scale: [0, 1],
  autoplay: true,
  easing: 'linear',
};

const tlHome = anime.timeline({
  autoplay: false,
  duration: 500
});

tlHome.add(preloaderAnim)
.add(mainCenterAnim)
.add(mainTitle1Anim)
.add(mainTitle2Anim)
.add(logoAnim)
.add(mainLineAnim)
.add({
  targets: '.home-main-counter__num',
  opacity: ['0', '1'],
  translateY: ['10%', '0%'],
  easing: 'linear',
  duration: 400,
  delay: anime.stagger(100),
});
tlHome.play();

