//works links animation

let worksLinkAnim;

if(window.matchMedia("(min-width: 992px)").matches) {
  worksLinkAnim = {
    targets: '.works__link',
    translateY: ['150%', '0%'],
    easing: 'linear',
    duration: 600,
    delay: anime.stagger(200),
  };
} else {
  worksLinkAnim = {
    targets: '.works__link',
    translateX: ['-400%', '0%'],
    easing: 'linear',
    duration: 600,
    delay: anime.stagger(200),
  };
}

const tlWorks = anime.timeline({
  autoplay: false,
});

tlWorks.add(preloaderAnim)
.add(worksLinkAnim);

tlWorks.play();

//article prev, tasks and list animation

const articlePrevTasks = document.querySelectorAll('.article-preview__task');
const articlePrevTitleMini = document.querySelectorAll('.article-preview__title-mini');

articlePrevTasks.forEach(task => {
  const waypoint = new Waypoint({
    element: task,
    handler: function() {
      const taskTranslation = anime({
        targets: task,
        opacity: ['0', '1'],
        translateY: ['50%', '0%'],
        easing: 'linear',
        duration: 500
      });
    },
    offset: '100%',
  });
});

articlePrevTitleMini.forEach(task => {
  const waypoint = new Waypoint({
    element: task,
    handler: function() {
      const titleMiniTranslation = anime({
        targets: task,
        opacity: ['0', '1'],
        translateY: ['-50%', '0%'],
        easing: 'linear',
        duration: 500
      });
    },
    offset: '100%',
  });
});

const previewList  = document.querySelectorAll('.article-preview__list');
const articlePreview = document.querySelector('.article-preview');
const workMain = document.querySelector('.work-main');
let previewItem;
let listAnim;

previewList.forEach(list => {
  const listScroll = new Waypoint({
    element: list,
    handler: function() {
      if(articlePreview) {
        previewItem = document.querySelectorAll('.article-preview .article-preview__item');
        listAnim = anime({
          targets: previewItem,
          opacity: ['0', '1'],
          easing: 'linear',
          duration: 200,
          delay: anime.stagger(200),
        });
      } else if(workMain) {
        previewItem = document.querySelectorAll('.work-main .article-preview__item');
        listAnim = anime({
          targets: previewItem,
          opacity: ['0', '1'],
          easing: 'linear',
          duration: 200,
          delay: anime.stagger(200, {start: 3500}),
        });
      }
    },
    offset: '100%',
  });
});

